/**
 * spei.repository.js
 *
 * This file serves as a repository for SPEI transaction data. It uses the
 * spei.api.js module to fetch data and translates it into domain models.
 */
import {speiApi} from './spei.api.js';
import {SpeiTransaction} from '../models/spei-transaction.model.js';
import {WebhookNotification} from '../models/webhook-notification.model';

export const speiRepository = {
    /**
     * Retrieves a paginated and filtered list of SPEI transactions.
     * @param {object} params - Contains page, pageSize, and filters.
     * @returns {Promise<SpeiTransaction[]>}
     */
    getTransactions: async function (params) {
        const response = await speiApi.getTransactions(params);
        if (response.success && response.data && Array.isArray(response.data)) {
            return response.data.map(rawData => new SpeiTransaction(rawData));
        } else {
            if (!response.success) {
                console.error('Failed to retrieve transactions from repository:', response.error.message);
            } else {
                console.error('API response.data is not in the expected array format:', response.data);
            }
            return [];
        }
    },

    /**
     * Retrieves a single transaction and its related webhooks.
     * @param {string} transactionId The ID of the transaction to fetch.
     * @returns {Promise<{transaction: SpeiTransaction, webhooks: WebhookNotification[]}|null>}
     */
    getTransactionById: async function (transactionId) {
        const response = await speiApi.getTransactionById(transactionId);
        if (response.success && response.data) {
            const transaction = new SpeiTransaction(response.data);
            let webhooks = [];
            // Check for the nested webhooks array and map it to the model
            if (response.data.webhooks && Array.isArray(response.data.webhooks)) {
                webhooks = response.data.webhooks.map(wh => new WebhookNotification(wh));
            }
            return { transaction, webhooks };
        } else {
            console.error('Failed to retrieve transaction by ID:', response.error?.message);
            return null;
        }
    }
};
