import {homeUI} from './ui/home.ui.js';
import {authService} from '../auth/domain/auth.service.js';
import {homeService} from "./domain/home.service";
import {customerService} from "../customer/domain/customer.service";
import {customerUI} from "../customer/ui/customer.ui";

$(document).ready(() => {
    // Render the top bar user profile on the home page.
    if ($('#user-profile-home').length > 0) {
        const user = authService.getUserProfile();
        if (user) {
            homeUI.renderTopBarUserProfile(user);
        } else {
            console.error("No user profile data found.");
        }
    }


    // Renders the user profile section on the user profile page.
    if ($('#user-profile-page').length > 0) {
        const user = authService.getUserProfile();
        if (user) {
            homeUI.renderUserProfile(user);
        } else {
            console.error("No user profile data found.");
        }
    }


    if ($('#spei-dashboard-page').length > 0) {
        // Load the dashboard data when the page is ready

        async function loadSpeiDashboardData() {
            try {
                const dashboardData = await homeService.getDashboardData();
                if (dashboardData) {
                    homeUI.renderSpeiDashboardSummary(dashboardData);
                    $('#dashboard-skeleton-loader').hide();
                    $('#dashboard-content').fadeIn();
                } else {
                    $('#dashboard-skeleton-loader').hide();
                    $('#dashboard-content').html(`
            <div class="alert alert-danger" role="alert">
                <i class="mdi mdi-block-helper me-2"></i>
                Could not load dashboard data. Please try again later.
            </div>
        `).show();
                }
            } catch (error) {
                console.error("Error loading dashboard data:", error);
                $('#dashboard-skeleton-loader').hide();
                $('#dashboard-content').html(`
            <div class="alert alert-danger" role="alert">
                <i class="mdi mdi-block-helper me-2"></i>
                Could not load dashboard data. Please try again later.
            </div>
        `).show();
            }
        }

        loadSpeiDashboardData();
    }


    if ($('#checkout-dashboard-page').length > 0) {
        // Load the dashboard data when the page is ready

        async function loadSpeiDashboardData() {
            try {
                const dashboardData = await homeService.getDashboardData();
                if (dashboardData) {
                    homeUI.renderCheckoutDashboardSummary(dashboardData);
                    $('#dashboard-skeleton-loader').hide();
                    $('#dashboard-content').fadeIn();
                } else {
                    $('#dashboard-skeleton-loader').hide();
                    $('#dashboard-content').html(`
            <div class="alert alert-danger" role="alert">
                <i class="mdi mdi-block-helper me-2"></i>
                Could not load dashboard data. Please try again later.
            </div>
        `).show();
                }
            } catch (error) {
                console.error("Error loading dashboard data:", error);
                $('#dashboard-skeleton-loader').hide();
                $('#dashboard-content').html(`
            <div class="alert alert-danger" role="alert">
                <i class="mdi mdi-block-helper me-2"></i>
                Could not load dashboard data. Please try again later.
            </div>
        `).show();
            }
        }

        loadSpeiDashboardData();
    }

    $('#send-support-email').on('click', function (e) {
        console.log("Support email button clicked.");
        // This will open the user's default email client
        // with the 'To' field pre-filled.
        e.preventDefault();
        window.location.href = 'mailto:support@test.com';
    });
});
