import {homeRepository} from "../data/home.repository";

export const homeService = {

    /**
     * Retrieves the dashboard data for the authenticated user.
     * @returns {Promise<DashboardSummary>} A promise that resolves with the dashboard summary.
     */
    getDashboardData: async function () {
        try {
            return await homeRepository.getDashboardData();
        } catch (error) {
            console.error('Error fetching dashboard data:', error);
            throw error; // Re-throw to handle it in the calling context
        }
    }
}
