import { customerApi } from './customer.api.js';
import { CustomerMinified } from '../models/customer-minified.model.js';
import {CustomerDetails} from "../models/customer-details.model";

export const customerRepository = {

    /**
     * Retrieves a list of CustomerMinified objects.
     * @returns {Promise<CustomerMinified[]>} A promise that resolves with an array of CustomerMinified instances.
     */
    getAllCustomers: async function () {
        const response = await customerApi.getCustomers();

        if (response.success && response.data && response.data.customers && Array.isArray(response.data.customers)) {
            // FIX: Map the 'customers' array inside the 'data' object
            return response.data.customers.map(rawData => new CustomerMinified(rawData));
        } else {
            console.error('Failed to retrieve customers from repository:', response.error?.message || 'Unexpected response format.');
            return [];
        }
    },

    /**
     * Fetches a specific customer by their ID.
     * @param customerId {number} The ID of the customer to fetch.
     * @returns {Promise<void>}
     */
    getCustomerById: async function (customerId) {
        const response = await customerApi.getCustomerById(customerId);

        if (response.success && response.data) {
            // Map the 'customer' object to a CustomerMinified instance
            return new CustomerDetails.fromDTO(response.data);
        } else {
            console.error('Failed to retrieve customer by ID:', response.error?.message || 'Unexpected response format.');
            throw new Error('Customer not found or invalid response format.');
        }
    }
}
