/**
 * api.client.js
 * * A centralized, intelligent client for all backend API communications.
 * - Automatically adds authentication headers ('Authorization', 'x-api-key').
 * - Handles the token refresh lifecycle.
 * - Parses the standard API response structure { message, data, error }.
 * - Maps business logic errors to user-friendly messages.
 * - Returns a standardized ApiResponse object for predictable handling in the app.
 */
import {apiErrorMap} from './api.errors.js';
import env from '../config/env.js';

// A standardized response object for the application layers to use.
class ApiResponse {
    constructor(success, data, error = null) {
        this.success = success;
        this.data = data;
        this.error = error; // error is an object { code, message }
    }
}

const apiClient = {
    TRANSACTION_BASE_URL: env.TRANSACTION_BASE_URL,
    USERS_BASE_URL: env.USERS_BASE_URL,
    OAUTH_BASE_URL: env.OAUTH_BASE_URL,

    /**
     * Performs the actual AJAX request, including auth and error handling.
     * This is a private method intended for internal use by get, post, etc.
     */
    async _request(endpoint, method, body = null) {
        let accessToken = localStorage.getItem('accessToken');

        // --- Internal function to handle token refreshing ---
        const handle401AndRetry = async () => {
            console.warn('Access token expired or invalid. Attempting to refresh...');
            const refreshToken = localStorage.getItem('refreshToken');
            if (!refreshToken) {
                console.error('No refresh token available. Redirecting to login.');
                window.location.href = '/auth/login.html'; // Or your app's login page
                return new ApiResponse(false, null, {code: 'NO_REFRESH_TOKEN', message: 'No refresh token.'});
            }

            try {
                // Make the call to your refresh token endpoint
                const refreshResponse = await $.ajax({
                    url: `${this.OAUTH_BASE_URL}/token/refresh`, // <-- UPDATE REFRESH ENDPOINT
                    method: 'POST',
                    contentType: 'application/json',
                    data: JSON.stringify({refreshToken})
                });

                // Assuming the refresh endpoint returns a standard API response with new tokens in the data field
                if (refreshResponse.error) {
                    throw new Error(refreshResponse.error);
                }

                const newAccessToken = refreshResponse.data.accessToken;
                localStorage.setItem('accessToken', newAccessToken);
                console.log('Token refreshed successfully.');

                // IMPORTANT: Retry the original request with the new token
                return this._request(endpoint, method, body);
            } catch (refreshError) {
                console.error('Failed to refresh token. Redirecting to login.', refreshError);
                localStorage.clear(); // Clear stale tokens
                window.location.href = '/auth/login.html';
                return new ApiResponse(false, null, {
                    code: 'AUTH_REFRESH_FAILED',
                    message: apiErrorMap['AUTH_REFRESH_FAILED']
                });
            }
        };

        // --- Main AJAX Call ---
        try {
            const response = await $.ajax({
                url: endpoint,
                method: method,
                contentType: 'application/json',
                headers: {
                    'Authorization': `Bearer ${accessToken}`,
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                    'SESSID': 'asdasdasdasdasd'
                },
                data: body ? JSON.stringify(body) : null
            });

            // Handle business logic errors returned in a 2xx response body
            if (response.error) {
                const errorCode = response.error;
                const errorMessage = apiErrorMap[errorCode] || apiErrorMap['DEFAULT'];
                console.error(`API Business Error: ${errorCode} - ${errorMessage}`);
                return new ApiResponse(false, null, {code: errorCode, message: errorMessage});
            }

            // If everything is successful
            return new ApiResponse(true, response.data, null);

        } catch (error) {
            // Handle HTTP level errors (e.g., 401, 404, 500)
            if (error.status === 401) {
                return await handle401AndRetry();
            }

            const errorMessage = `HTTP Error ${error.status}: ${error.statusText}`;
            console.error(errorMessage, error.responseJSON);
            return new ApiResponse(false, null, {code: `HTTP_${error.status}`, message: errorMessage});
        }
    },

    /** Public Methods (GET, POST, etc.) **/
    get: function (endpoint) {
        return this._request(endpoint, 'GET');
    },

    post: function (endpoint, body) {
        return this._request(endpoint, 'POST', body);
    },

    put: function (endpoint, body) {
        return this._request(endpoint, 'PUT', body);
    },

    delete: function (endpoint) {
        return this._request(endpoint, 'DELETE');
    }
};

export {apiClient, ApiResponse};
